@import url(https://fonts.googleapis.com/css?family=Fjalla+One|Lilita+One|Rajdhani);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
@import url(https://fonts.googleapis.com/css?family=Lato:300);
import

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}


body,html{
  height: 100%;
  margin: 0;
}




code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


h1{
  font-size: 50px;

}

h2{
  margin: 0;
  color:#4c4c4c;
  font-weight: 400;

}



@font-face {
  font-family: "San Francisco";
  font-weight: 400;
  src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-regular-webfont.woff");
}


.App {
  text-align: center;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}


p{
  font-family: 'Open Sans Condensed', sans-serif;

  color: #ffffff;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


h1{
  font-family: 'Lilita One', cursive;
  font-weight: 400
}
h2{
  font-family: 'Amatic SC', cursive;
}

h3,h4,h5{
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  
}

h4{
  font-weight: 600;
}
h5{
  font-weight: 400;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


#header_wrapper{

    position: absolute;
    height: 100%;
    top: 0;

    max-height: 100%;



    right: 0;
    left: 0;

    
    padding:0 200px 0 200px;


}

p{

    font-family: 'Lato', sans-serif;
    
    font-size: 20px;
    margin: auto;
    text-align: left;
    margin-left: 20px;
}
#logo_wrapper{
    
    display: -webkit-flex;
    
    display: flex;
    height: 100%;
    

}

#logo{
    margin : auto;
    min-width: 30%;
}
#brandWrapper{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin-bottom: 20px;
}
.brand{
    max-width: 150px;
    padding:20px;
    max-height: 50px;

}
#cesar_logo{
    height: auto; 
    width: auto; 
    padding-bottom: 6px;
    min-height: 70px;
    
}



@media only screen and (max-width: 1100px) {
    #logo{
        max-width: 150px;
        padding-top: 60px;
    }

    #header_wrapper{
        padding: 0 10% 0 10%;
        overflow-y: scroll;
        overflow-x: hidden;
        display: inline-grid
       
        
    }

    #brandWrapper{
        position: relative;
        margin: 20px 0 20px 0;
    }
    #logo_wrapper{
        display: block;
    }
    p{
        text-align: center;
        padding-top:25px;
    }
    
}





#menu_wrapper{
    width: 100%;
    height: 50px;
    
    display: -webkit-flex;
    
    display: flex;
    position: fixed;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    top: 0;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
    -webkit-align-items: end;
            align-items: end;

    transition: 0.3s;
}


@media only screen and (max-width: 600px) {
    #menu_wrapper{
        -webkit-justify-content: center !important;
                justify-content: center !important;
    }
}

.anchor{
    width: auto;
    height: 100%;
    box-shadow: none;
    display: -webkit-flex;
    display: flex;
    cursor:pointer;
    margin: 0 10px 0 10px;
    
    transition: 0.3s;
    -webkit-align-items: center;
            align-items: center;
}



.anchor h3{
    color: #f0f8ff;
}





.progressWrapper{
    text-align: left;
    overflow: paged-y;
    word-break: break-word;
    box-shadow: 3px 6px 15px 2px rgba(0,0,0,0.41);

    max-width: 400px;

    margin: 0px 20px 20px 0;

}

.progressWrapper h3 ,h4{
    margin-left: 15px;

    font-weight: 300;

}


@media only screen and (max-width: 1100px) {
    .progressWrapper{
        margin: 0 0 20px 0;
    }
}

.imageWrapper{
    position: relative;
    background-color:#6cde5f;
    height: 50px;
    z-index: -1;


}


.progressWrapper img {
    position: relative;



    max-width: 300px;
}

.progressWrapper h3{
    position: absolute;
    margin-bottom: 6px !important;
    bottom: 0;
    color: #4a4747;

}
.progressWrapper h4{
    color: #717171
}



.dividerWrapper{

    padding: 20px 60px 20px 60px;
}


.peopleWrapper h3,h4,h5{
    margin: 8px;
    color: #ffffff;
}


.peopleWrapper{
    
    -webkit-flex: 1 0 21%;
    
            flex: 1 0 21%;

}


.peopleWrapper img {
    max-width: 150px;
    border-radius: 50%;
}

.peopleWrapper h5 {
    margin-bottom: 40px!important;
}


.photoWrapper{
    max-width: 400px;
    max-height: 400px;
    overflow: hidden;    
    -webkit-flex: 1 0 21%;    
            flex: 1 0 21%;
}


.photoWrapper img{
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.photoWrapper img:hover{
    -webkit-transform: scale(1.2,1.2);
            transform: scale(1.2,1.2)
}

.imageViwerWrapper{
    position:fixed;
    width:100%;
    height:100%;
    background-color: rgba(229,229,229, 0.85);
    z-index:9;
    top:0;
    left:0;
    bottom:0;
    
    
    
}
@media only screen and (max-width: 1100px) {

    #imageSource{
        max-width: 100%;
    }
}


#imageSource{
    width: auto;   
}

